<template>
    <BaseView>
        <template #title>
            <BaseHead level="1">Спонсоры</BaseHead>
        </template>

        <template #actions>
            <div class="filter-actions">
                <FieldSearch
                    placeholder="Поиск спонсоров"
                >
                    <template #actionEnd>
                        <BaseLink @click.stop="modalParams = true">Параметры</BaseLink>
                    </template>
                </FieldSearch>
                <div
                    v-if="filteredLevels.length > 0 || filter.start.start || filter.end.start"
                    class="filter-actions__filters"
                >
                    <BaseTag
                        v-for="level in filteredLevels"

                        class="filter-actions__filter"
                        :text="level.text"
                        removable
                        @remove="onFilterClearLevel(level.value)"
                    />
                    <BaseTag
                        v-if="filter.start.start"
                        class="filter-actions__filter"
                        removable
                        @remove="onFilterClearDate('start')"
                    >
                        Начало: {{ filter.start.start | formatDatetime }} — {{ filter.start.end | formatDatetime }}
                    </BaseTag>
                    <BaseTag
                        v-if="filter.end.start"
                        class="filter-actions__filter"
                        removable
                        @remove="onFilterClearDate('end')"
                    >
                        Окончание: {{ filter.end.start | formatDatetime }} — {{ filter.end.end | formatDatetime }}
                    </BaseTag>
                </div>
            </div>
        </template>

        <template v-if="sponsors == 0">
            <ElementPlaceholder
                :icon="require('../assets/emoji/public-address-loudspeaker.png')"
            >
                Пока что еще нет спонсоров. Расскажите Вашему сообществу о возможных уровнях спонсорства и мотивируйте пользователей различными наградами
            </ElementPlaceholder>
        </template>
        <template v-else>
            <BaseHead level="3">
                Текущие спонсоры

                <template #description>
                    <DropdownSelectable
                        label="Сортировка:"
                        :options="[
                            {
                                text: 'По дате начала спонсорства — сначала первые',
                            },
                            {
                                text: 'По дате начала спонсорства — сначала последние',
                                selected: true,
                            },
                            {
                                text: 'По дате окончания спонсорства — сначала первые',
                            },
                            {
                                text: 'По дате окончания спонсорства — сначала последние',
                            },
                            {
                                text: 'По продолжительности спонсорства — сначала самые долгие',
                            },
                            {
                                text: 'По продолжительности спонсорства — сначала самые краткие',
                            }
                        ]"
                    />
                </template>
            </BaseHead>

            <div class="sponsors">
                <template v-for="sponsor in sponsors">
                    <div class="sponsors__avatar">
                        <ElementAvatar src="https://sun3-12.userapi.com/c836333/v836333001/31192/y1Cm4JfplhQ.jpg"/>
                    </div>
                    <div class="sponsors__info">
                        <BaseLink>Павел Дуров</BaseLink>
                        <BaseMessage>Уровень Silver</BaseMessage>
                    </div>
                    <div class="sponsors__start">
                        <div class="sponsors__label">
                            Начало спонсорства
                        </div>
                        {{ Date.now() | formatDatetime }}
                    </div>
                    <div class="sponsors__end">
                        <div class="sponsors__label">
                            Окончание спонсорства
                        </div>
                        {{ Date.now() + 1000 * 60 * 60 * 24 * 30 | formatDatetime }}
                    </div>
                    <div class="sponsors__actions">
                        <BaseDropdown
                            :options="[
                                {
                                    text: 'Показать всех с уровнем Silver',
                                },
                                {
                                    text: 'Добавить комментарий',
                                    click: () => modalComment = true,
                                },
                            ]"
                        >
                            <BaseButton
                                type="tertiary"
                                mode="rounded"
                            >
                                <template #prepend>
                                    <font-awesome-icon icon="ellipsis-h"/>
                                </template>
                            </BaseButton>
                        </BaseDropdown>
                    </div>
                    <div class="sponsors__line"></div>
                </template>
            </div>
        </template>

        <BaseModal
            :show.sync="modalParams"
            title="Параметры"
        >
            <BaseForm @submit="onSelectParams">
                <BaseFormField
                    type="select"
                    label="Уровень"
                    placeholder="Выберите уровень"
                    name="level"
                    multiple
                    :options="levels"
                    :selected="filter.levels"
                />
                <BaseFormField
                    type="datetime"
                    name="date_start"
                    label="Начало спонсорства"
                    placeholder="Выберите даты"
                    :start="filter.start.start"
                    :end="filter.start.end"
                    range
                    timepicker
                >
                    <template #message>
                        Выбор пользователей, которые стали спонсором в указанном диапазоне дат
                    </template>
                </BaseFormField>
                <BaseFormField
                    type="datetime"
                    name="date_end"
                    label="Окончание спонсорства"
                    placeholder="Выберите даты"
                    :start="filter.end.start"
                    :end="filter.end.end"
                    range
                    timepicker
                >
                    <template #message>
                        Выбор пользователей, которые перестанут быть спонсором в указанном диапазоне дат
                    </template>
                </BaseFormField>
                <div class="form__actions">
                    <BaseFormButton label="Показать"/>
                    <BaseButton
                        label="Сбросить"
                        type="tertiary"
                        @click="onFilterClear"
                    />
                </div>
            </BaseForm>
        </BaseModal>

        <BaseModal
            :show.sync="modalComment"
            title="Павел Дуров"
        >
            <BaseForm @submit="onAddComment">
                <BaseFormField
                    type="textarea"
                    label="Комментарий"
                    :maxlength="100"
                    placeholder="Например: данный спонсор хотел бы видеть новые тематики в моем творчестве"
                >
                    <template #message>
                        Небольшой текст для личных заметок. Комментарий будет виден только руководителям Вашего сообщества
                    </template>
                </BaseFormField>
                <BaseFormButton label="Готово"/>
            </BaseForm>
        </BaseModal>
    </BaseView>
</template>

<script>
import FieldSearch from '../components/FieldSearch';
import ElementAvatar from '../components/ElementAvatar';
import DropdownSelectable from '../components/DropdownSelectable';
import ElementPlaceholder from '../components/ElementPlaceholder';

export default {
    name: 'Sponsors',
    components: {
        ElementPlaceholder, DropdownSelectable, ElementAvatar, FieldSearch,
    },

    data() {
        return {
            modalParams: false,
            modalComment: false,

            filter: {
                user: '',
                levels: [],
                start: {
                    start: null,
                    end: null,
                },
                end: {
                    start: null,
                    end: null,
                },
            },

            levels: [
                {
                    text: 'Silver',
                    value: 1,
                },
                {
                    text: 'Gold',
                    value: 2,
                },
                {
                    text: 'Diamond',
                    value: 3,
                },
                {
                    text: 'Ыть))',
                    value: 31,
                },
            ],

            sponsors: 10,
        };
    },

    computed: {
        filteredLevels() {
            return this.levels.filter((level) => this.filter.levels.includes(level.value.toString()));
        },
    },

    methods: {
        onSelectParams(formData, finishLoading) {
            this.modalParams = false;

            this.filter.levels = formData.get('level').split(',');
            this.filter.start = {
                start: formData.get('date_start_start'),
                end: formData.get('date_start_end'),
            };

            this.filter.end = {
                start: formData.get('date_end_start'),
                end: formData.get('date_end_end'),
            };
        },

        onFilterClear() {
            this.filter.levels = [];
            this.onFilterClearDate('start');
            this.onFilterClearDate('end');

            this.modalParams = false;
        },

        onFilterClearLevel(value) {
            this.filter.levels = this.filter.levels.filter((level) => level !== value);
        },

        onFilterClearDate(type) {
            this.filter[type].start = null;
            this.filter[type].end = null;
        },

        onAddComment() {
            this.modalComment = false;
        },

        formatDatetimeRange(from, until) {
            const result = `${this.$format.datetime(from)} — ${this.$format.datetime(until)}`;
            return this.$format.nbsp(result);
        },
    },
};
</script>

