<template>
    <div class="dropdown-selectable">
        <span v-if="label" class="dropdown-selectable__label">{{ label }}</span> <span></span>
        <BaseDropdown
            class="dropdown-selectable__dropdown"
            :options="optionsWithoutSelected"
            @select="onSelect"
        >
            <BaseLink>{{ text }}</BaseLink> <font-awesome-icon icon="chevron-down" class="dropdown-selectable__icon"/>
        </BaseDropdown>
    </div>
</template>

<script>
export default {
    name: 'DropdownSelectable',

    props: {
        options: Array,
        label: String,
    },

    data() {
        return {
            selected: {},
        };
    },

    computed: {
        text() {
            return this.selected.text ? this.selected.text.toLowerCase() : '';
        },

        optionsWithoutSelected() {
            return this.options.filter((option) => option != this.selected);
        },
    },

    created() {
        this.options.some((option) => {
            if (option.selected) {
                this.selected = option;
                return true;
            }
        });
    },

    methods: {
        onSelect(option) {
            this.selected = option;
        },
    },
};
</script>

<style scoped lang="less">
    .dropdown-selectable {
        display: inline;

        /deep/ &__dropdown {

            &, .dropdown__parent {
                display: inline;
            }
        }

        &__label,
        &__icon {
            color: var(--text_type_secondary);
        }

        &__icon {
            font-size: .8em;
        }
    }
</style>
